import { css, styled } from '../../utils/styled';
import { withLinkStyle } from '../../utils/styles';
import { color, modifier } from '../../utils/theme';
import type { PressableProps } from '../Pressable';
import { Pressable } from '../Pressable';

export type LinkProps = PressableProps;

export const Link = styled(Pressable)<LinkProps>`
    ${withLinkStyle};

    ${modifier(
        ({ href }: PressableProps) => !href,
        css`
            &:disabled {
                cursor: not-allowed;
                color: ${color('content.disabled')};
            }
        `,
    )}
`;

Link.displayName = 'Link';
